<template>

		<div style="height: 100%;" :id="echartName"></div>


	</div>
</template>
<script>
	export default {
		name: "timeline",
		props: ["echartName", "datas",'img','textlist','total','colorList'],
		data() {
			return {
				datajson: {},
				myChart: {},
				optionData: [],
				showBol: false,

				colorList2:['#007eff', '#0090eb','#00a8ee', '#00d8ff', '#01e3eb','#234cfd']
			};
		},
		created() {},
		mounted() {
			this.beforeInitEchart();
		},
		methods: {
			beforeInitEchart() {

				this.initEchart(this.datas);
			},
			initEchart(optionData) {
				var echartIdName = this.echartName;
				this.myChart = this.$echarts.init(document.getElementById(echartIdName));
				this.myChart.clear();

//				if(this.img){
//					var image = new Image();  
//						image.src = this.img;  
//					var giftImageUrl ="";
//					var _this=this;
//						image.onload = function(){  
//						   giftImageUrl = _this.getBase64Image(image); 
//							_this.renderEchart(optionData,giftImageUrl);
//						   
//						}  
//				}
				this.renderEchart(optionData);


			},
			getBase64Image(img) {  

			     var canvas = document.createElement("canvas");  
			     canvas.width = img.width;  
			     canvas.height = img.height;  
			     var ctx = canvas.getContext("2d");  
			     ctx.drawImage(img, 0, 0, img.width, img.height);  
			     var ext = img.src.substring(img.src.lastIndexOf(".")+1).toLowerCase();  
			     var dataURL = canvas.toDataURL("image/"+ext);  
			     return dataURL;  
			},  
			renderEchart(optionData) {
				var scaleData = optionData
				var rich = {
					white: {
						color: '#ddd',
						align: 'center',
						padding: [3, 0]
					}
				};
				var placeHolderStyle = {
					normal: {
						label: {
							show: false
						},
						labelLine: {
							show: false
						},
						color: 'rgba(0, 0, 0, 0)',
						borderColor: 'rgba(0, 137, 255, 0.5)',
						borderWidth: 10
					}
				};
				var data = [];
				var color = this.colorList;

				for(var i = 0; i < scaleData.length; i++) {
					data.push({
						value: scaleData[i].value,
						name: scaleData[i].name,
						itemStyle: {
							normal: {
								borderWidth: 10,
								borderColor: {
								    type: 'linear',
								    x: 0,
								    y: 0,
								    x2: 0,
								    y2: 1,
								    colorStops: [{
								        offset: 0, color:this.colorList[0] // 0% 处的颜色
								    }, {
								        offset: 1, color:this.colorList[1] // 100% 处的颜色
								    }],
								    globalCoord: false // 缺省为 false
								},
							}
						}
					}, {
						value:this.total-scaleData[i].value,
						name: '',
						itemStyle: placeHolderStyle
					});
				}
				var seriesObj = [{
					name:'bg',
					type:'pie',
					radius:'80%',
					hoverAnimation: false,
					hoverOffset:0,
					clockWise: false,
					selectedOffset: 0,
					labelLine: {
						show: false
					},
					data:[{
						value:335,
						type:'pie',
						name:'',zlevel:0,
						itemStyle:{
		                    color:'rgba(0,49,105,.5)'
		                }
					}],
					itemStyle: {
						normal: {
							label: {
								show: false
							},
							labelLine: {
								show: false
							}
						},
					
					}
				},{
					name: '',
					type: 'pie',
					
					startAngle:180,
					radius: ['65%', '65%'],
					hoverAnimation: false,
					selectedOffset: 0,
					zlevel:9,
					itemStyle: {
						normal: {
							label: {
								show: false
							},
							labelLine: {
								show: false
							}
						}
					},
					data: data
				},{
					name:'bg',
					type:'pie',
					radius:'50%',
					hoverAnimation: false,
					hoverOffset:0,
					clockWise: false,zlevel:10,
					selectedOffset: 0,
					labelLine: {
						show: false
					},
					data:[{
						value:335,
						type:'pie',
						name:'',
						itemStyle:{
		                    color:'rgba(2,56,128,.6)'	
		                }
					}],
					itemStyle: {
						normal: {
							label: {
								show: false
							},
							labelLine: {
								show: false
							}
						},
					
					}
				}];
				var option = {
					graphic: {
				        elements: [{
				            type: 'text',
				            style: {
				                width: 40,
				                height: 30,
				                fontSize:18,
				                fill: '#fff',
				                textAlign:'center',
				                text: this.textlist.join('\n'),
				                font: '14px Microsoft YaHei'

				            },
				            left: 'center',
				            top: 'center',
				            zlevel:999
				        }]
				    },
					tooltip: {
						show: false
					},
					legend: {
						show: false
					},
					toolbox: {
						show: false
					},
					series: seriesObj
				}
				this.myChart.setOption(option);

			}
		}
	};
</script>
<style scoped>
	.echartCenter {
		position: relative;
		/*background: url('../../assets/detalBg.png') no-repeat center center;*/
		font-family: "微软雅黑", "microsoft yahei";
		background-size: 167px;
	}
	
	.echartCenter span {
		position: absolute;
		color: #fff;
		font-size: 14px;
	}
	.echartCenter p{
		white-space: nowrap;
		font-size: 18px;
		text-indent: 18px;
	}
	.echartCenter p ,.msg big{
		color: white;
	}
	.msg{
		text-align: left;
		border-bottom: 1px solid rgba(255,255,255,.2);
	}
	.msg big{
		font-size: 26px;
		font-family: 'DINOT';
		padding-right: 5px;
	}
	.msg h4{
		font-weight: normal;
		color: #c8c8c8;
	}
	.circle{
		display: inline-block;
		border-radius: 10px;
		width: 10px;
		height: 10px;
		background: #fff;		
	}
</style>


