<template>
	<div class="p">
		<div class="left-bg">
			<div class="comMsg">
				<img src="../assets/img/title/rc-hdzt.png"/>

				<el-row :gutter="10" class="percent-box" style="margin-top: 50px;">
					<h5>海外人才行</h5>
					
					<el-col :span="8" v-for="(item,index) in FaceTotal" :key="index">
						<div style="height: 125px;">
							<circleHalf :ids="'face'+index" :data='[item]' :total="58"> </circleHalf>
						</div>
						<p style="text-align: center;margin: 0;">{{item.name}}</p>
					</el-col>
				</el-row>

				<el-row :gutter="10" class="percent-box" style="margin-top: 50px;">
					<h5>国内人才行</h5>
					<el-col :span="8" v-for="(item,index) in FaceTotal1" :key="index">
						<div style="height: 125px;">
							<circleHalf :ids="'face2'+index" :data='[item]' :total="58"> </circleHalf>
						</div>
						<p style="text-align: center;margin: 0;">{{item.name}}</p>
					</el-col>
				</el-row>
				
				<el-row :gutter="10" class="percent-box" style="margin-top: 50px;">
					<h5>国内城市行</h5>
					<el-col :span="8" v-for="(item,index) in FaceTotal2" :key="index">
						<div style="height: 125px;">
							<circleHalf :ids="'face3'+index" :data='[item]' :total="58"> </circleHalf>
						</div>
						<p style="text-align: center;margin: 0;">{{item.name}}</p>
					</el-col>
				</el-row>
	
				
			</div>
		</div>
		<div class="right-bg">
			<div class="safe-width" style="float: right;margin-right: 40px;padding-top: 180px;">
				<img src="../assets/img/title/rc-dyfx.png"/>
				<el-row>
					<el-col :span="12">
						<div class="echartline" style="height: 250px;position: relative;">
							<h5 style="margin-top:15px;position: absolute;text-align: center;width: 100%;padding: 0;">覆盖国家</h5>
		            		<firstText echartName="fgl" :textlist="['75%','覆盖率']" :total="100"  :colorList="['#0054ff', '#00d8ff']" class="my-chart-box" ref="detailMsg1" img="/static/img/xf.png" :datas="fgl" v-if="Basicgeoinfo.length>0"></firstText>                       		
						</div>
					</el-col>
					<el-col :span="12">
						<div class="echartline" style="height: 250px;position: relative;">
							<h5 style="margin-top: 15px;position: absolute;text-align: center;width: 100%;padding: 0;">覆盖国内城市</h5>
		            		<firstText echartName="fgl2" :textlist="['89%','覆盖率']"  :total="100" :colorList="['#00beff', '#00ffd2']" class="my-chart-box" ref="detailMsg2" img="/static/img/xf.png" :datas="fgl2" v-if="Basicgeoinfo.length>0"></firstText>                       		
						</div>
					</el-col>
				</el-row>
				<img src="../assets/img/title/rc-ryhx.png"/>
				
				<div style="height: 280px;">
					<h5 style="margin-bottom: -40px;position: absolute;">年龄构成</h5>
					<myBar ids="projNum" ref="projNum" :data="barDatas" :color="colorLists"></myBar>		
				</div>

				<div class="echartline" style="height: 250px;">
					<h5 style="margin-bottom: -40px;position: absolute;">学历构成</h5>
					
            		<first echartName="detailMsg" class="my-chart-box" ref="detailMsg" img="/static/img/pie.png" :datas="Basicgeoinfo" v-if="Basicgeoinfo.length>0"></first>                       		
				</div>
			</div>
			

		</div>
	</div>	
</template>

<script>
import myBar from '@/components/module/echarts/echartBarDD.vue'
	import circleHalf from '@/components/module/echarts/circleHalf'
	import first from '@/components/module/echarts/first.vue'
	import firstText from '@/components/module/echarts/firstText.vue'
	export default{
		components:{
			myBar,
			circleHalf,
			first,
			firstText
		},
		data(){
			return{
				barDatas:{
					title:"",
					data:[[345,486,216,335,353,126]],
					dataAxis:['20岁','25岁','30岁','35岁','40岁','45岁'],
					unit:'',
					legend:['']
				},
				Basicgeoinfo: [
		        	{ value: 22,unit:'%',name:'博士' },
		        	{ value: 18,unit:'%',name:'硕士' },
		        	{ value: 14,unit:'%',name:'本科' },
		        	{ value: 16,unit:'%',name:'大专' },
		        	{ value: 30,unit:'%',name:'海归博士' }
		        ],
		        fgl:[{ value: 75,unit:'%',name:'覆盖率' }],
		        fgl2:[{ value: 75,unit:'%',name:'覆盖率' }],
				comData:{
					comTotal:8567,
					regist:356,
					serve:6451

				},
				FaceTotal:[{
				    'name': '举办次数',
				    'value': 45,
				    'unit':'次'
				},{
				    'name': '参与人数',
				    'value': 7486,
				    'unit':'人'
				},{
				    'name': '覆盖国家',
				    'value': 25,
				    'unit':'个'
				}],
				FaceTotal1:[{
				    'name': '举办次数',
				    'value': 65,
				    'unit':'次'
				},{
				    'name': '参与人数',
				    'value': 7283,
				    'unit':'人'
				},{
				    'name': '覆盖城市',
				    'value': 34
				}],
				FaceTotal2:[{
				    'name': '举办次数',
				    'value': 23,
				    'unit':'次'
				},{
				    'name': '参与人数',
				    'value': 9254,
				    'unit':'人'
				},{
				    'name': '覆盖城市',
				    'value': 56
				}],
				colorLists:['#0084ff',"#0084ff"],
				
			}
		}
	}
</script>

<style scoped="scoped" lang="less">
	.company{
		color: #fff;
		margin-top: 170px;
		margin-right: 40px;
		float: right;
		& h3{
			line-height: 2em;
			margin-top: 60px;
		}
		& h3,label{
			font-size: 24px;
		}
		& span{
			font-size: 20px;
		}
		& p{
			margin: 15px 0;
		}
	}
	.echartline{
		height: 250px;
		width: 100%;
	}
	.buildMsg{
		width: 100%;
		p{border-bottom: 2px solid rgba(255,255,255,.24);text-align: right;}
		& span{
			width: 10em;
			font-size: 22px;
			display: inline-block;
			text-align: left;
	        margin-top:10px;
			float: left;
		}
		& label{
			 background: linear-gradient(to top, #00c0ff, #0090ff);
	        -webkit-background-clip: text;
	        color: transparent;
	        font-size: 40px;
			width: 3em;
			display: inline-block;
			font-weight: bold;
			text-align: left;
			font-family: 'DINOT';
		}
		& small{
			font-size: 20px;
			 background: linear-gradient(to top, #00c0ff, #0090ff);
	        -webkit-background-clip: text;
	        color: transparent;
	        width: 3em;
	        display: inline-block;
	        text-align: right;
	        float: right;
	        margin-top:10px;
	        font-family: 'DINOT';

		}
	}
	.percent-box{
		img{width: 100%;}
	}
	.comMsg{
		margin-left:40px;
	}
	.p h5{
		font-size: 16px;
		 background: linear-gradient(to top, #00c0ff, #0090ff);
        -webkit-background-clip: text;
        color: transparent;
       	padding-left: 1.4em;
       	margin-bottom: 10px;
	}
	.comMsg p{
		color: #fff;
	}
	.p{
		position: absolute;
		width: 100%;
		height: 100%;
		background: url(../assets/img/rc-bg.jpg)no-repeat;
		background-size: 100% 100%;
		top: 0;
	}
</style>


